import React, { Component } from 'react';
import { Container, Row, Col } from 'reactstrap';
import MyCarousel_1 from './CarouselComponent_1';
import MyCarousel_2 from './CarouselComponent_2';
import MyCarousel_3 from './CarouselComponent_3'




class Home extends Component {
    constructor(props){
        super(props);
        this.state = {}
    };

    render() {

        return(
            <React.Fragment>
                <div className="Container-fluid">
                    <div className="row row-content">
                        <div className="col-md">
                            <h5 className="m-5">Rooms</h5>
                            <p className="m-5">Enjoy the tranquility of having the something all to yourself</p>
                        </div>
                        <MyCarousel_1 />
                    </div>   
                    <div className="row row-content">
                            <MyCarousel_2 />
                        <div className="col-md">
                            <h5 className="m-5">Amenities</h5>
                            <p className="m-5">Eating yummies and growing tummies.</p>
                        </div>
                    </div>
                    <div className="row row-content">
                        <div className="col-md">
                            <h5 className="m-5">Nightlife</h5>
                            <p className="m-5">Enjoy the tranquility of having the something all to yourself</p>
                        </div>
                        <MyCarousel_3 />
                    </div>   
                </div>
            </React.Fragment>
        )
    }
}

export default Home;

